/*------------------------------------*\
	Headings
\*------------------------------------*/
h1,
h2,
h3,
h4,
h5 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  color: #2d333a;
  text-rendering: optimizelegibility;
  margin: 1em 0; }

h1, .alpha {
  font-size: 36px;
  font-size: 2rem; }
  h1 img, .alpha img {
    width: 60px;
    margin-right: 10px; }
  h1 a, h1 a:hover, .alpha a, .alpha a:hover {
    color: #2d333a;
    text-decoration: none; }

h2, .bravo {
  font-size: 30px;
  font-size: 1.66667rem; }

h3, .charlie {
  font-size: 24px;
  font-size: 1.33333rem; }

h4, .delta {
  font-size: 20px;
  font-size: 1.11111rem; }

h5, .echo {
  font-size: 16px;
  font-size: 0.88889rem; }

/*------------------------------------*\
	Images
	inc. Google maps fix (https://github.com/inuitcss/spacing.images/blob/master/_spacing.images.scss)
\*------------------------------------*/
img {
  max-width: 100%;
  vertical-align: middle; }

.gm-style img,
img[width],
img[height] {
  max-width: none; }

/*------------------------------------*\
	Links
\*------------------------------------*/
a {
  color: #ff3f4c;
  text-decoration: underline; }

a:focus,
a:hover {
  color: #bf000d;
  text-decoration: underline; }

/*------------------------------------*\
	Lists
\*------------------------------------*/
/**
 * Ordered list
 */
ol {
  padding-left: 1em; }

/**
 * Unordered list
 */
ul {
  padding-left: 1em; }

/**
 * Definition list
 */
dt {
  margin-top: 1em;
  font-weight: bold; }

dd {
  margin-left: 0; }

/*------------------------------------*\
	Page
\*------------------------------------*/
html {
  color: #2d333a;
  font-size: 18px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  line-height: 26px; }

body {
  background-color: #fff; }

/*------------------------------------*\
	Typography
\*------------------------------------*/
p {
  margin: 0 0 1em; }

p.lead {
  font-size: 120%; }

/*------------------------------------*\
	Quotes
\*------------------------------------*/
blockquote {
  margin: 0 0 1em;
  padding: 0 1em;
  border-left: 4px solid #eeeeee; }

blockquote footer {
  color: #999;
  font-size: 14px;
  font-size: 0.77778rem; }

/*------------------------------------*\
	Tables
\*------------------------------------*/
table {
  margin-bottom: 1em; }

table th,
table td {
  padding: 0.5em 1em;
  border-bottom: 1px solid #ccc;
  text-align: left;
  -webkit-font-smoothing: antialiased; }

table th {
  border-width: 2px; }

table tr:last-child td {
  border-bottom: none; }

table tr:nth-child(even) {
  background: #eee; }

table tbody tr:hover {
  background: #f9f9f9; }

/*------------------------------------*\
	Objects
	Cosmetic-free design patterns
\*------------------------------------*/
/*------------------------------------*\
	Wrappers
\*------------------------------------*/
.wrapper {
  max-width: 620px;
  margin: 3em auto;
  padding: 0 1em; }

/*------------------------------------*\
	Vendor
	Third-party styles
\*------------------------------------*/
/*@import "vendor/plugin";*/
/*------------------------------------*\
	Components
	Designed components, chunks of UI
\*------------------------------------*/
/*------------------------------------*\
	Header
\*------------------------------------*/
[role=banner] {
  text-align: center; }

/*------------------------------------*\
	Footer
\*------------------------------------*/
[role=contentinfo] {
  text-align: center; }

/*------------------------------------*\
	Book Club
\*------------------------------------*/
.book-list {
  list-style: none;
  margin: 0;
  padding: 0; }
  .book-list li {
    margin-bottom: 1em; }
  .book-list a {
    font-weight: bold; }

.author {
  display: block;
  color: #999;
  font-size: 0.8em;
  line-height: 1.2em; }

/*------------------------------------*\
	Buttons
\*------------------------------------*/
.btn-block {
  text-align: center;
  margin: 40px 0; }

.btn {
  display: block;
  box-sizing: border-box;
  height: 45px;
  padding: 0 50px;
  border: 0px solid #fff;
  border-radius: 8px;
  color: #fff;
  background-color: #ff3f4c;
  font-weight: 400;
  line-height: 45px;
  text-align: center;
  white-space: nowrap;
  text-decoration: none;
  margin: 5px;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0px 4px 4px 0px rgba(10, 10, 10, 0.05);
  transition: all 0.2s ease; }
  .btn:hover, .btn:focus {
    border-color: #888;
    outline: 0;
    color: #fff;
    text-decoration: none;
    position: relative;
    top: -1px;
    box-shadow: 0px 8px 4px 0px rgba(10, 10, 10, 0.1); }
  @media (min-width: 30em) {
    .btn {
      display: inline-block; } }

/*------------------------------------*\
	Trumps
	Helpers and overrides
\*------------------------------------*/
/*------------------------------------*\
	Media
	Print styles
\*------------------------------------*/
@media print {
  /*------------------------------------*\
	Print
\*------------------------------------*/
  * {
    background: #ffffff;
    color: #000000 !important; }
  html {
    font: 100%/1.5 Arial, serif; }
  /**
 * Hide
 */
  .header,
  .footer {
    display: none; }
  /**
 * Make full-width
 */
  .unit {
    margin: 0 !important;
    padding: 0 !important;
    width: 100% !important; } }
